.svg-intro {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-color: #ffffff; /* 배경색 */
  }
  
  .intro-svg path {
    fill: none;
    stroke: #000; /* 검은색 선 */
    stroke-width: 2;
    stroke-linecap: round;
  }

  path {
    stroke: black;
    stroke-width: 2;
    fill: none;
    stroke-dasharray: 300;
    stroke-dashoffset: 300;
    animation: draw 2s linear forwards;
  }

  @keyframes draw {
    to {
      stroke-dashoffset: 0;
    }
  }